import React from "react";
import _ from "lodash";
import { createComponent } from "webiny-app";
import styles from "./Widget.scss?prefix=Webiny_CMS_Row_Widget";

class RowWidget extends React.Component {
    render() {
        const {
            layout,
            modules: { Grid, Icon },
            widget
        } = this.props;

        return (
            <Grid.Row>
                {widget.data.map(({ grid, widget }, index) => (
                    <Grid.Col key={index} md={grid}>
                        <div className={styles.editorItem}>
                            {!widget && (
                                <span className={styles.addBtn}>
                                    <Icon icon={"plus-circle"} size={"2x"} />{" "}
                                    <span className={styles.txt}>ADD CONTENT</span>
                                </span>
                            )}
                        </div>
                    </Grid.Col>
                ))}
            </Grid.Row>
        );
    }
}

export default createComponent(RowWidget, { modules: ["Grid", "Link", "Icon"] });
